 body {
  background: url(/static/media/background.f2d8e15e.jpg) no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
} 

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.page-header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 30px;
}

/* Login Page */
.title-text, #loginSlogan {
  font-style: italic;
  text-align: right;
}

.title-div {
  text-align:right;
}

#loginSlogan { 
  font-size: 20px;
  font-weight: 700;
}

/* Saved Reel Page */
li {
  list-style-type: none; 
}

ul.buttons,
ul.list-group {
  list-style-type: none;
  padding-bottom: 30px;
}

.back-to-dashboard {
  text-align: center;
}
.loginCard{
    background-color: #ffffff;
    padding: 0 20px;
    width: 90%;
    max-width: 800px;
    height: 550px;
    margin: 50px auto 0 auto;
    position: relative;
}

.copyright {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    color: #404040;
}

@media only screen and (min-width: 450px) {
    .default_card {
       width:60%;
    }
}
.logo-container {
    position: relative;
}

img {
    width: 125px;
    margin: 20px 0;
}

.spacer {
    flex: 1 1; /* This will take all available space */
}
#username {
    width: 100%;
    margin: 0 auto;
}

#password {
    width:100%;
    margin: 0 auto;
}

.formErrors {
    color: red;
    font-size: 1em;
}

.formErrors p {
    margin-bottom: 0;
}

form {
    max-width: 60%;
    margin: 0 auto;
}

.button-container {
    text-align: center;
}

#login_btn, #new-user{
    width: 200px;
    padding: 10px 20px;
    margin: 10px auto 0 auto;
    background-color: #c3666b;
    border: 0;
    color: #ffffff;
}

#login_btn:hover, #new-user:hover{
    background-color: #a4494e;;
}

#login_btn {
    margin-right: 10px;
}

#new-user {
