.saved-content {
    margin-top: 30px;
    height: 520px;
    padding: 15px;
    overflow-y: scroll;
    background: rgb(248, 237, 237);
}

.movie-image {
    width: 250px;
    height: 320px;
}

.time-stamp {
    font-size: 17px;
}

.star-rating {
    text-align: center;
    padding: 0;
}


ul {
    display: flex;
    justify-content: center;
    padding: 0;
}

.movie-synopsis {
    overflow-y: scroll;
    height: 160px;
}

::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 7px;
}

::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, .5);
    -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

.movie-title {
    font-weight: bolder;
    margin-bottom: 20px;
    font-style: italic;
}

#synopsis {
    font-weight: bolder;
    font-style: italic;
}

#saved-recipe-head {
    font-style: italic;
    font-weight: bolder;
    font-size: 20px;
}

#saved-recipe {
    font-style: italic;
    font-weight: bolder;
}
#share-button {
  width: 120px;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: #585757;
  border: 0;
  color: #ffffff;
  margin-left: 30px;
  margin-right: 30px;
}

.copy-toast {
  z-index: 99;
  position: absolute;
  margin-top: 20px;
  margin-right: 50px;
  margin-left: 30px;
}

.toast.show {
  opacity: 0.7;
}

.toast-header {
  background: rgb(9, 82, 151);
  color: white;
  font-size: 15px;
}
